import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Platform } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {

  userData={};
  status='';
  status2 = 'Sin definir';

  constructor(public navCtrl: NavController,public plt: Platform) {}

  ionViewDidLoad(){

    this.getUserPosition().then(available => {

    window.geofence.initialize().then((initStatus) => {
    console.log("Geofence inicializado OK", initStatus);
    this.definirTransition();
    }).catch((error) => {
    console.error(error);
    });
    }, error => console.log(error));
  }

  addGeofence() {

    let fence = {
      id: '69ca1b88', //any unique ID
      latitude:       8.8948483, //center of geofence radius
      longitude:      -84.2410,
      radius:         2000, //radius to edge of geofence in meters
      transitionType: 2, //see 'Transition Types' below
      notification: { //notification settings
        id:             1,//any unique ID
        title:          'test titulo', //notification title
        text:           'test body', //notification body
        openAppOnClick: true //open app when notification is tapped
      }
    }
    window.geofence.addOrUpdate(fence).then(()=>{
    console.log('Geofence agregado');
    this.status2 = 'Fence agregado';
    },function(err){console.log('Geofence failed to add');});
  }

  definirTransition(){

    window.geofence.onTransitionReceived =  (geofences)=>{
    this.status = 'Transicion detectada!';
     this.status2 = '';
    geofences.forEach(function (geo) {
    console.log("Geofence transition detected", geo);
    });};

    window.geofence.onNotificationClicked = function (notificationData) {
    console.log("App opened from Geo Notification!", notificationData);
    };

    this.status = 'Geofence configurado OK';
  }

   getUserPosition() {
    return new Promise(resolve => {
      const HIGH_ACCURACY = 'high_accuracy';
      cordova.plugins.diagnostic.isLocationEnabled(enabled => {
        if (enabled) {
          cordova.plugins.diagnostic.getLocationMode(locationMode => {
            if (locationMode === HIGH_ACCURACY) {
              navigator.geolocation.watchPosition(pos => {
              console.log(pos);
                resolve({
                  coords: {
                    latitude: pos.coords.latitude,
                    longitude: pos.coords.longitude
                  }
                });
              },error => {resolve(error)},{
                timeout: 30000,
                maximumAge: 0,
                enableHighAccuracy: true
              });
            } else {
              this.askForHighAccuracy().then(available => {
                if (available) {
                  this.getUserPosition().then(a => resolve(a), e => resolve(e));
                }
              }, error => resolve(error));
            }
          },error => resolve(error));
        } else {
          cordova.plugins.locationAccuracy.request(result => {
            if (result) {
              this.getUserPosition().then(result => resolve(result), error => resolve(error));
            }
          },error => {
            resolve(error)
          },1);
        }
      }, error => {
        resolve(error)
      });

    });
  }

  askForHighAccuracy(): Promise<Geoposition> {
    return new Promise(resolve => {
      cordova.plugins.locationAccuracy.request(() => {
        navigator.geolocation.watchPosition(position => {
            resolve(position)},
            error => { resolve(error)},
            {timeout: 30000});
      },error => {resolve(error)},cordova.plugins.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY);
    });
  }

}



